<template>
  <section>
   
    <div class="zx-banner">
      <img src="../../../static/images/zx_banner2.jpg" alt="">
      <div class="zx-sqc">
        <span class="zx-sq-co clearfix"><em></em><p>行业分析专栏</p><em class="zx-sqce"></em></span>
        <span class="zx-sq-ctxt">随心写作，自由表达</span>
      </div>
    </div>
    <div class="inner clearfix">
      <ul class="zx-myh-mzl zx-xz-ar">
        <li class="clearfix">
          <span><em>*</em>题图</span>
          <span class="zx-xz-arim"><img style="width:848px;height:253px" src="../../../static/images/cg_nbg.jpg" id="textPic" alt="" ref='imgshow' ></span>
        </li>
        <li class="clearfix">
          <span><em>*</em>标题</span>
          <input type="text" class="c-int"  v-model.trim="title">
        </li>
        <li class="clearfix">
          <span><em>*</em>内容</span>
          <span class="zx-xz-arim"><UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue" v-model='txt'></UE></span>
        </li>
      </ul>
      
      <div class="zx-butt zx-xz-butt">
        <button type="button" class="zx-btn-bc" @click="getUEContent">发表</button>
        <button type="button" class="zx-btn-bc scroll_s" @click="prieve">预览</button>
        <button type="button" class="zx-btn-bc zx-btn-fh">返回</button>
      </div>
    </div>
     <div class="zhezao" v-if="isShow"> </div>
      <div class="zx-pop-box zx-myhr-zz-r" style="position:absolute;top:40%;z-index:1002;opacity:1 " @scroll.prevent v-if="isShow">
        <a class="zx-close1" @click="close">×</a>
        <h1><a href="javascript:;" v-text="title"></a></h1>
        <div>
          <img src="mycrudeoil/images/zx_img2.png" alt="">专栏名称<em class="zx-pop-zl">{{columnTitle}}</em>
          <!-- <span>
  			      <i class="zx-icon zx-ico-yl"></i>5680
  			      <i class="zx-icon zx-ico-sc1"></i>50
  			      <i class="zx-icon zx-ico-dz"></i>88
  			    </span> -->
        </div>
        <div v-html ='html1'></div>
        <div class="zx-pop-art">
          <!-- 分享：<a href=""><i class="zx-icon zx-ico-wb"></i></a><a href=""><i class="zx-icon zx-ico-xl"></i></a> -->
          <!-- <span>
  			      <a href=""><i class="zx-icon zx-ico-lik"></i>喜欢<em>1</em></a>
  			      <a href="" class="zx-pop-asc"><i class="zx-icon zx-ico-sc1"></i>收藏<em>11</em></a>
  			    </span> -->
        </div>
      </div>
     
   
  </section>
</template>

<script>
  
  
  import {
 saveArticle,
 articleDetails,
} from "../../api/info";
import UE from "../../components/ue.vue";
  export default {
    data() {
      return {
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 350
        },
        ue1: "ue1", // 不同编辑器必须不同的id
       
        title: "",
        txt: "",
        isShow:false,
        picshow: "",
        columnid:'',
        html1:'',
        uuid:'',
       defaultMsg:"",
       columnTitle:"",
       imgUrl:'',
      };
    },
 components: {
      UE
    },
    created(){
      this.uuid=this.$route.query.uuid;
      this.getlist()
      
    },
    
    methods: {
      getlist(){
        if(this.uuid){
          var param = {
          infoUuid:this.uuid
        }
        var self = this
        articleDetails(param).then(res=>{
                console.log(res.datas)
                this.title = res.datas.title;
                this.defaultMsg = res.datas.tex;
                setTimeout(function(){//子组件赋值
                   self.$refs.ue.setUEContentTxt()
                },500)
              
              
                 this.$refs.imgshow.src =  res.datas.extend1
            })
        }
        
      },
      getUEContent() {
        // if (this.imgurl == "" || this.title == "" || this.txt == "") {
        //   this.$message("题图,标题，内容不能为空");
        // }
        let title = this.title;
        let img = this.$refs.imgshow.src;
        let html = this.$refs.ue.getUEContent(); // 调用子组件方法
     
        let content = this.$refs.ue.getUEContentTxt();
        let id = this.columnid;
        let uuid = this.uuid;
        if(title != ''&&html != ''&&content != ''){
          
          var param={
              title:title,
              tex:content,
              texHtml:html,
              extend1:img,
              bizId:id,
              uuid:uuid
          }
            saveArticle(param).then(res=>{
                console.log(res)
                this.$message("保存成功");
                this.$router.go(-1)
            })
        }else{
          this.$message("题图,标题，内容不能为空");
        }
        //		        this.$notify({
        //		          title: '获取成功，可在控制台查看！',
        //		          message: content,
        //		          type: 'success'
        //		        });
      },
      // getUEContentTxt() {
      //   let content = this.$refs.ue.getUEContentTxt(); // 调用子组件方法
      //   this.$notify({
      //     title: "获取成功，可在控制台查看！",
      //     message: content,
      //     type: "success"
      //   });
      //   console.log(content);
      // },
      prieve() {
         console.log(this.imgUrl,'2222222222222222')
         var self = this 
        let title = this.title;
        //let img = this.$refs.imgshow.src;
        let html = this.$refs.ue.getUEContent(); // 调用子组件方法
        let content = this.$refs.ue.getUEContentTxt();
         this.html1 = html
        let id = this.columnid;
        this.defaultMsg = html
        if(title != ''&&html != ''&&content != ''){
          window.scrollTo(0,0)

          this.isShow = true;
          self.picyulan("textPic", $("#textPic"), this.picshow);
         
        }else{
          this.$message("题图,标题，内容不能为空");
        }
        

      },
      close() {
        this.isShow = false;
       
      
      },
     
    },
    
    mounted() {
      
      // $('.scroll_s').click(function(){
      //   $(document.body).css({"overflow":"scroll","overflow-x":"hidden"})
      // })
      var self = this
      this.columnid=this.$route.query.id;
      this.columnTitle=this.$route.query.columnTitle; 
      
      picyulan("textPic", $("#textPic"), self.picshow);
      function picyulan(btn, imgs, dataiten) {
        var times = new Date().getTime();
        //实例化一个plupload上传对象
        var uploader = new plupload.Uploader({
          browse_button: btn, //触发文件选择对话框的按钮，为那个元素id
          runtimes: "html5,flash,silverlight,html4",
          //multi_selection: false,
          //container: document.getElementById('container'),
          flash_swf_url: "/static/upload/Moxie.swf",
          silverlight_xap_url: "/static/upload/Moxie.xap",
          url: "http://oss.aliyuncs.com",
          filters: {
            //设置允许上传的类型
            max_file_size: "100mb", //设置允许上传的最大文件的大小
            mime_types: [
              //用来限定上传文件的类型,一个类型一个对象，放入当前数组
              {
                title: "Image files",
                extensions: "jpg,png,tif,bmp"
              }
            ]
          }
        });
        //在实例对象上调用init()方法进行初始化
        uploader.init();
        //当文件添加到上传队列后触发监听函数参数
        uploader.bind("FilesAdded", function(uploader, files) {
          var s = random_string();
          var geshi = get_suffix(files[0].name);
          var picname = s + geshi;
          var times = new Date().getTime();
          $.ajax({
            url: configUitl.getOssServerPath("", "info") + "&" + times,
            async: false,
            success: function(data) {
              var data1 = {
                key: "info/" + picname + "",
                policy: data.policy,
                OSSAccessKeyId: data.accessid,
                success_action_status: "200", //让服务端返回200,不然，默认会返回204
                //'callback' : null,
                signature: data.signature
              };
              var picshow = data.host + "/info/" + picname;
              dataiten = picshow;
              uploader.setOption({
                url: data.host,
                multipart_params: data1
              });
            }
          });
          uploader.start(); //开始上传
        });
        //当队列中的某一个文件上传完成后触发监听函数参数
        uploader.bind("FileUploaded", function(uploader, file, responseObject) {
          imgs[0].src = dataiten;
          self.imgUrl = dataiten
         
        });
      }
      function random_string(len) {
        //生成隨機字符
        len = len || 32;
        var chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
        var maxPos = chars.length;
        var pwd = "";
        for (var i = 0; i < len; i++) {
          pwd += chars.charAt(Math.floor(Math.random() * maxPos));
        }
        return pwd;
      }
      function get_suffix(filename) {
        //獲取文件類型後綴
        var pos = filename.lastIndexOf(".");
        var suffix = "";
        if (pos != -1) {
          suffix = filename.substring(pos);
        }
        return suffix;
      }
    }
    
   
  };
</script>

<style>
  #edui1_bottombar {
    display: none !important;
  }
  #ue1.edui-default .edui-box {
    height: 22px;
  }
  #ue1 .edui-combox-body.edui-default {
    height: 20px;
  }
  .zhezao{
    /* position: absolute;   
    top: 0%;   
    left: 0%;    */
    width:100%;   
    height:100%;   
    background-color: black;   
    z-index:1001;   
    -moz-opacity: 0.3;   
    opacity:.30;   
    filter: alpha(opacity=30);  
    position: fixed;
    left: 0;
    bottom: 0;
  }
  
</style>


